body
	font-family: 'Roboto', sans-serif
	background-color: #000
	color: #fff
.container
	max-width: 1330px
	width: 100%
	margin: 0 auto
	padding: 0 15px
.header
	background-image: url(../images/background.png)
	background-repeat: no-repeat
	background-size: cover
.header__inner
	position: fixed
	display: flex
	justify-content: space-between
	max-width: 1780px
	width: 100%
	padding: 40px 15px 0 15px
	margin: 0 auto
	align-items: center
.inner__list
	display: flex
	&-link
		font-size: 16px
		line-height: 150%
		color: #FFFFFF
		transition: all .3s
.br
	padding-right: 50px
.inner__list-link:hover
	opacity: 70%
.button
	font-size: 16px
	line-height: 150%
	color: #FFFFFF
	background: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%)
	border-radius: 50px
	padding: 10px 30px
	&:hover
		border: 4px solid #000
		padding: 5px 26px
		color: #000
.header__title
	font-size: 64px
	line-height: 140%
	font-weight: 700
	padding: 270px 0 50px 0
.time__list
	display: flex
	flex-wrap: wrap
	padding-bottom: 218px
.time__column
	flex-direction: column
	background-color: #121212
	text-align: center
	margin-right: 30px
	&-singl
		font-weight: bold
		font-size: 36px
		line-height: 140%
		padding: 20px 30px 0px 30px
	&-text
		font-size: 16px
		line-height: 140%
		opacity: 0.4
		padding-bottom: 20px

/*-=-=-=-=-=-=--==-=-=-=-=-=-=-=-=-=-=-=-==-=-=-=-=-=*/

.blak__line
	background-color: #121212
.blak__body
	display: flex
	justify-content: space-between
	align-items: center
.batton2
	font-weight: bold
	font-size: 16px
	line-height: 150%
	color: #FFFFFF
	padding: 10px 30px
	background: linear-gradient(94.78deg, #DF5950 11.19%, #451046 93.72%)
	border-radius: 50px
	margin: 43px 0
.blak__l1-link
	font-size: 16px
	line-height: 150%
	opacity: 0.4
.blak__l2-link
	font-size: 16px
	line-height: 150%
.blak__box-list1
	display: flex
.blak__box-list2
	display: flex
	justify-content: space-between
.new-blak__box-list2
	font-size: 16px
	line-height: 150%
	padding-left: 30px
.line-blak
	padding: 8px 0
.new-br
	padding-bottom: 20px

/*-=-=-=-=-=-=-=-=-=-=-=-=-=--=-==-=-=-=-=-=-=-==-=--=*/

.cont1__body
